import React from 'react';

interface ButtonProps {
  onClick(): void;
  className: string;
  children: HTMLElement | null;
}

const Button: React.FC<ButtonProps> = (props: ButtonProps) => {
  const { className, children = '' } = props;
  return (
    <button
      className={className}
      type="button"
      onClick={() => { props.onClick(); }}
    >
      {children}
    </button>
  );
};

export default Button;
