import React from 'react';
import './Switcher.css';

interface SwitcherProps {
  onClick(): void;
}

const Switcher: React.FC<SwitcherProps> = (props: SwitcherProps) => (
  <div className="switcher-container">
    <label className="switcher-label" htmlFor="game-mode">
      <input className="switcher-input" id="game-mode" type="checkbox" onClick={() => { props.onClick(); }} />
      <span className="switcher-text" data-on="play" data-off="train" />
    </label>
  </div>
);

export default Switcher;
