import React from 'react';
import { NAV_MENU_ROUTES } from '../../../constants/constants.ts';
import './NavMenu.css';

interface NavMenuProps {
  /* eslint-disable no-unused-vars */
  onClickNavItem: (route: string) => void;
  /* eslint-enable no-unused-vars */
  onClickPageCover: () => void;
  activeRout: string;
  isHiddenNavMenu: boolean;
}

const NavMenu: React.FC<NavMenuProps> = (props: NavMenuProps) => {
  const { activeRout, isHiddenNavMenu } = props;
  const navItems = NAV_MENU_ROUTES.map((route: string) => (
    <li
      className={`sidebar__nav-item ${route === activeRout ? 'active' : ''}`}
      key={route}
    >
      <a
        onClick={() => { props.onClickNavItem(route); }}
        href={`#/${route}`}
      >
        {route}
      </a>
    </li>
  ));
  return (
    <>
      <div
        className={`page-cover ${isHiddenNavMenu ? 'hidden' : ''}`}
        onClick={() => { props.onClickPageCover(); }}
        role="none"
      />
      <ul className={`sidebar__nav-menu ${isHiddenNavMenu ? 'hidden' : ''}`}>
        {navItems}
      </ul>
    </>
  );
};

export default NavMenu;
