import React, { Component } from 'react';
import './MainPage.css';

interface CategoryInterface {
  name: string;
  image: string;
}

interface MainPageState {
  cardsArray: CategoryInterface[];
}

interface MainPageProps {
  /* eslint-disable no-unused-vars */
  onClickCategoryCard: (route: string) => void;
  /* eslint-enable no-unused-vars */
}

class MainPage extends Component<MainPageProps, MainPageState> {
  constructor(props: MainPageProps) {
    super(props);
    this.state = {
      cardsArray: [],
    };
  }

  componentDidMount(): void {
    this.getCategoriesInfo();
  }

  getCategoriesInfo = async (): Promise<void> => {
    const response = await fetch('./cards.json');
    const cards = await response.json();
    const cardsArray = await cards[0].categories;
    this.setState({ cardsArray });
  }

  createCards = (cardsArray: CategoryInterface[]): JSX.Element[] => {
    const { onClickCategoryCard } = this.props;
    return (cardsArray).map((card: CategoryInterface): JSX.Element => (
      <li
        key={card.name}
        className="main-page__card-container card-container"
      >
        <div className="main-page__card card">
          <div className="main-page__card-front card-front">
            <div
              className="main-page__image"
              style={{ backgroundImage: `url(${card.image})` }}
            />
            <a
              className="main-page__card-link"
              href={`#/${card.name}`}
              onClick={() => onClickCategoryCard(card.name)}
            >
              <div className="main-page__card-text card-text">{card.name}</div>
            </a>
          </div>
        </div>
      </li>
    ));
  };

  render(): JSX.Element {
    const { cardsArray } = this.state;
    return (
      <ul className="main-page__container cards-page-container">
        {this.createCards(cardsArray)}
      </ul>
    );
  }
}

export default MainPage;
